.container {
  padding: 20px;
}

.container h1 {
  color: #fff;
  font-size: 20px;
}
.container h1 span {
  color: rgb(199, 120, 221);
}

.listContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.list {
  width: 280px;
  border: 1px solid #fff;
  border-radius: 5;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 8px;
  list-style: none;
  color: #fff;
}

.listItem {
  padding: 6px;
  box-shadow: 2px 2px 2px #bbb;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #f4f4f4;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
